const Table = (
  {
    rankData,
    pagination,
    title
  }
) => {
  const { Column } = ATable;

  // 
  const handleSpeed = (speed) => {
    let time = 0;

    if (speed < 60) {
      speed % 1 === 0
        ? time = `0:${Math.round((speed) * 100) / 100}:00`
        : time = `0:${Math.round((speed) * 100) / 100}`;
    } else if (speed >= 60 && speed < 70) {
      speed % 1 === 0
        ? time = `1:0${Math.round((speed - 60) * 100) / 100}:00`
        : time = `1:0${Math.round((speed - 60) * 100) / 100}`;
    } else if (speed >= 70 && speed < 120) {
      speed % 1 === 0
        ? time = `1:${Math.round((speed - 60) * 100) / 100}:00`
        : time = `1:${Math.round((speed - 60) * 100) / 100}`;
    } else if (speed >= 120 && speed < 130) {
      speed % 1 === 0
        ? time = `2:0${Math.round((speed - 120) * 100) / 100}:00`
        : time = `2:0${Math.round((speed - 120) * 100) / 100}`;
    } else if (speed >= 130 && speed < 180) {
      speed % 1 === 0
        ? time = `2:${Math.round((speed - 120) * 100) / 100}:00`
        : time = `2:${Math.round((speed - 120) * 100) / 100}`;
    } else {
      time = '';
    }

    return time;
  };

  return (
    <ATable
      title={() => title}
      bordered={true}
      dataSource={rankData}
      size={'small'}
      sticky={true}
      tableLayout={'fixed'}
      scroll={{ x: 'max-content' }}
      pagination={pagination}
      onRow={(val) => {
        return {
          className: val.speed < 70
            ? `${styles.kbracer} ${styles.kbracer1}`
            : val.speed < 74
              ? `${styles.kbracer} ${styles.kbracer2}`
              : val.speed < 78
                ? `${styles.kbracer} ${styles.kbracer3}`
                : val.speed < 80
                  ? `${styles.kbracer} ${styles.kbracer4}`
                  : val.speed < 85
                    ? `${styles.kbracer} ${styles.kbracer5}`
                    : `${styles.kbracer} ${styles.kbracer6}`
        }
      }}
    >
      <Column
        title=""
        dataIndex="key"
        key="key"
        align="center"
      />
      <Column
        title=""
        dataIndex="car"
        key="car"
      />
      <Column
        title=""
        dataIndex="speed"
        key="speed"
        render={(text) => handleSpeed(text)}
      />
      <Column
        title=" (Ps)"
        dataIndex="hp_content"
        key="hp_content"
        align="center"
      />
      <Column
        title=""
        dataIndex="Powertrain"
        key="Powertrain"
      />
      <ColumnGroup title={() => ''}>
        <Column
          title=""
          dataIndex="tyre"
          key="tyre"
        />
        <Column
          title=""
          dataIndex="tyre_f"
          key="tyre_f"
          align="center"
          onCell={(res) => {
            if (res.tyre_f === res.tyre_r) {
              return {
                colSpan: 2,
              }
            }
          }}
        />
        <Column
          title=""
          dataIndex="tyre_r"
          key="tyre_r"
          align="center"
          onCell={(res) => {
            if (res.tyre_f === res.tyre_r) {
              return {
                colSpan: 0,
              }
            }
          }}
        />
      </ColumnGroup>
      <Column
        className={styles.black}
        title=" ()"
        dataIndex="temperature"
        key="temperature"
        align="center"
        width="50px"
      />
      <Column
        className={styles.black}
        title="km/h"
        dataIndex="limit"
        align="center"
        key="limit"
      />
      <Column
        className={styles.black}
        title="0-100s"
        dataIndex="accelerate"
        key="accelerate"
        align="center"
        width="75px"
      />
      <Column
        title=""
        dataIndex="Btitle"
        key="Btitle"
        render={(text, record) => record.BURL
          ? <a href={record.BURL}>{text}</a>
          : <p>{text}</p>}
      />
    </ATable>
  )
};

